.btn {
  display: inline-flex;
  justify-content: center;
  padding: 10px 20px;
  user-select: none;
  cursor: pointer;

  // color: $color-default-white;

  // background-color: $color-neon-blue;
  // border: none;


  transition: opacity $trans-default;

  // &--red {
  //   background-color: $color-torch-red;
  // }

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @include hover-focus {
    opacity: 0.8;
  }

  @include active {
    opacity: 0.3;
  }
}
